import React, { useState, useEffect } from 'react'
import userService from './services/users'
import Input from './components/Input'
import Select from './components/Select'

import Button from './components/Button'
import Users from './components/Users'

const App = () => {
  const [users, setUsers] = useState([])
  const [newNumber, setNewNumber] = useState('')
  const [newName, setNewName] = useState('')

  const [newEmail, setNewEmail] = useState('')
  const [newBrand, setNewBrand] = useState('')
  const [newDate, setNewDate] = useState('')
  const [newService, setNewService] = useState('')
  const [searchString, setSearchString] = useState('')

  useEffect(() => {
    console.log('effect')
    userService.getAll().then((initialUsers) => {
      console.log('promise fulfilled')
      setUsers(initialUsers)
    })
  }, [])
  const newOrder = (event) => {
    event.preventDefault()
    const orderObject = {
      customerName: newName,
      phoneNumber: newNumber,
      email: newEmail,
      bikeBrand: newBrand,
      typeOfService: newService,
      dueDate: newDate,
      id: users.length + 1,
    }
    const existingUser = users.find((u) => u.customerName.toLowerCase() === newName.toLowerCase())
    if (existingUser) {
      if (window.confirm(`${newName} is already added, replace the details?`)) {
        changeNumber(existingUser.id)
      }
    } else {
      userService.create(orderObject).then((returnedUser) => {
        setUsers(users.concat(returnedUser))
        setNewName('')
        setNewNumber('')
        setNewEmail('')
        setNewBrand('')
        setNewDate('')
        setNewService('')
      })
    }
  }

  const deleteButton = (user) => {
    if (window.confirm(`Delete ${user.customerName} ?`)) {
      userService
        .remove(user.id)
        .then(() => {
          setUsers(users.filter((u) => u.id !== user.id))
        })
        .catch((error) => {
          setUsers(users.filter((u) => u.id !== user.id))
          console.log(Error)
        })
    }
  }

  const handleNameChange = (event) => {
    setNewName(event.target.value)
  }

  const handleNumberChange = (event) => {
    setNewNumber(event.target.value)
  }

  const handleEmailChange = (event) => {
    setNewEmail(event.target.value)
  }

  const handleBrandChange = (event) => {
    setNewBrand(event.target.value)
  }

  const handleDateChange = (event) => {
    setNewDate(event.target.value)
  }

  const handleServiceChange = (event) => {
    setNewService(event.target.value)
  }

  const handleSearchStringChange = (event) => {
    setSearchString(event.target.value)
  }
  const changeNumber = (id) => {
    const user = users.find((u) => u.id === id)
    const newUserValues = {
      phoneNumber: newNumber,
      email: newEmail,
      bikeBrand: newBrand,
      typeOfService: newService,
      dueDate: newDate,
    }
    const changedUser = { ...user, ...newUserValues }

    userService.update(id, changedUser).then((returnedUser) => {
      setUsers(users.map((user) => (user.id !== id ? user : returnedUser)))
    })
  }

  const Options = ['wheel adjustment', 'chain replacement', 'brake maintenance']
  return (
    <div>
      <form className="container" onSubmit={newOrder}>
        <Input
          type={'text'}
          title={'Full Name'}
          name={'name'}
          handleChange={handleNameChange}
          value={newName}
          placeholder={'Enter your name'}
        />
        <Input
          type={'text'}
          title={'Phone Number'}
          name={'number'}
          handleChange={handleNumberChange}
          value={newNumber}
          placeholder={'Enter phone number'}
        />

        <Input
          type={'email'}
          title={'Email'}
          name={'email'}
          handleChange={handleEmailChange}
          value={newEmail}
          placeholder={'Enter Email'}
        />
        <Input
          type={'text'}
          title={'Brand'}
          name={'brand'}
          handleChange={handleBrandChange}
          value={newBrand}
          placeholder={'Enter brand'}
        />
        <Select
          title={'Services'}
          name={'services'}
          options={Options}
          value={newService}
          placeholder={'Select Services'}
          onChange={handleServiceChange}
        />
        <Input
          type={'date'}
          title={'Date'}
          name={'date'}
          handleChange={handleDateChange}
          value={newDate}
        />

        <Button title={'Submit'} type={'submit'} />
      </form>

      <h1>Filter shown with </h1>
      <input value={searchString} onChange={handleSearchStringChange} />
      <h1>Users</h1>
      <Users users={users} searchString={searchString} deleteButton={deleteButton} />
    </div>
  )
}
export default App

